.intro {
  &__title {
    font-size: 5em;
    line-height: 1.11;
    letter-spacing: -0.03em;
    font-weight: 700;
    text-align: center; }
  &__subtitle {
    margin-top: 20px;
    max-width: 260px;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    font-size: 1.8em;
    line-height: 1.55;
    letter-spacing: -0.03em; } }

.buttonStart {
  @include button;
  @include btnYellow;
  border-radius: 6px;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 50px;
  font-weight: 700;
  @include desktop {
    position: absolute; }
  &__icon {
    animation: bounceRight 2s infinite; } }

