// app
.container {
  positon: absolute;
  top: 0;
  left: 0;
  background: none;
  background-color: #ffffff;
  width: 100%;
  min-height: 100vh;
  padding-bottom: 100px;
  box-sizing: border-box;
  @include phablet {
 } }    // background-color: rgba($black, 0.75)
.content {
  position: relative;
  @include phablet {
    max-width: 420px;
    width: 420px;
    position: absolute;
    top: 50%;
    left: 50%;
    padding: 40px 15px 48px;
    transform: translate(-50%, -50%);
    background-color: #ffffff;
    box-shadow: 0.5px 0.5px 6px 6px rgba($black, 0.65); } }
.header {
  position: relative;
  &__icon {
    padding: 10px 15px 0; } }
.submit__button {
  width: 100%;
  padding-left: 15px;
  padding-right: 15px; }
.button {
  border: 1px solid red;
  position: relative;
  display: block;
  width: 100%;
  height: 44px;
  border-radius: 22px;
  background-color: #fe4066;
  font-size: 22px;
  color: #ffffff;
  font-weight: 500;
  text-align: center; }
.tip {
  position: relative;
  padding: 10px 15px 20px;
  font-size: 14px;
  line-height: 1.45;
  text-align: center; }
.signup-link {
  color: #FE4066;
  text-decoration: none;
  vertical-align: middle; }






