.details {
  position: relative;
  background-color: #ddd;
  padding: 10px;
  &__wrapper {
    border: 1px solid #777;
    background-color: $white;
    border-radius: 6px;
    display: flex;
    flex-flow: row wrap;
    position: relative; }
  &__label {
    position: relative;
    width: 50%;
    font-size: 1.6em;
    line-height: 1.33;
    padding-top: 14px;
    padding-bottom: 14px;
    text-align: left;
    padding-left: 10px; }
  &__taka {
    position: relative;
    width: 50%;
    padding-top: 14px;
    padding-bottom: 14px;
    display: inline-flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 10px;
    line-height: 1.33;
    font-size: 2em;
    font-weight: 700;
    &.joma {
      color: darken($green, 10%); }
    &.baki {
      color: $red; } }

  .contactButton {
    @include button;
    border-top: 1px solid #ddd;
    font-size: 1.4em;
    color: $gray;
    height: 44px;
    width: 100%; } }

.bakiButton,
.jomaButton {
  @include button;
  width: 50%;
  border-radius: 6px; }


.bakiButton {
  @include btnRed;
  margin-right: 10px; }

.jomaButton {
  @include btnGreen;
  margin-left: 10px; }

.stickbar {
  &__baki {
    width: 105px;
    text-align: center; }
  &__joma {
    width: 105px;
    text-align: center; } }

.detailsList {
  position: relative;
  &__item {
    position: relative;
    // border-bottom: 1px solid #ddd
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    text-decoration: none;
    color: $black;
    padding-left: 10px;
    // padding-right: 10px
    &::before {
      content: "";
      position: absolute;
      height: 1px;
      background-color: #ddd;
      left: 0;
      bottom: 0;
      right: 0;
      margin-bottom: -1px;
      margin-left: 10px;
      margin-right: 10px; } }

  &__date {
    font-size: 1.2em;
    line-height: 1.11;
    color: $light;
    flex: 1; }
  &__taka {
    font-size: 1.6em;
    line-height: 1.22;
    font-weight: 700;
    width: 105px * 2;
    text-align: right;
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px;
    &.baki {
      color: $red;
      padding-right: 105px + 10; }
    &.joma {
      color: darken($green, 10%);
      padding-right: 10px; }
    &::before {
      content: "";
      position: absolute;
      width: 50%;
      background-color: $lighter;
      left: 0;
      top: 0;
      height: 100%;
      z-index: -1; } } }

