
.formWrapper {
  position: relative;
  padding-top: 10px; }
.form {
  margin-top: 20px;
  position: relative;
  padding-left: 15px;
  padding-right: 15px;
  &:not(:last-child) {
    margin-bottom: 20px; }

  $root: &;
  &__title {
    display: block;
    color: $black;
    fill: $black;
    font-size: 30px;
    line-height: 1.33;
    font-weight: 500;
    margin-bottom: 20px;
    padding-left: 15px;
    padding-right: 15px; }
  &__label {
    display: block;
    color: #333;
    fill: #333;
    font-size: 20px;
    line-height: 1.33;
    font-weight: 400;
    letter-spacing: -0.03em;
    margin-bottom: 6px; }

  &__input {
    align-items: center;
    border: 1px solid transparent;
    border-radius: 4px;
    box-shadow: none;
    display: inline-flex;
    height: 40px;
    justify-content: flex-start;
    align-items: center;
    line-height: 1.5;
    padding: 6px 12px;
    position: relative;
    vertical-align: top;
    border-color: #E9E9E9;
    color: $black;
    box-shadow: none;
    max-width: 100%;
    width: 100%;
    font-size: 18px;
    font-weight: 700;
    transition: all 0.1s ease-out;
    margin: 0;

    &:focus,
    &:active {
      border-color: $black;
      box-shadow: none;
      outline: none;
      background-color: #fff; }

    @include placeholder {
      font-size: 16px;
      padding-top: 5px;
      padding-bottom: 8px;
      color: #bbbbbb; }
    &--error {
      border: 1px solid #ee395f; } }
  &__error {
    display: block;
    font-size: 12px;
    margin-top: 6px;
    margin-bottom: 0;
    padding-left: 15px;
    padding-right: 15px;
    color: darken($red, 10%);
    background-color: lighten($red, 35%);
    border: 1px solid lighten($red, 15%);
    border-radius: 4px; } }


