.buttonReport {
  @include button;
  @include btnPurple;
  padding-right: 14px;
  padding-left: 4px;
  border-radius: 500em;
  position: fixed;
  bottom: 0;
  right: 0;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 50px;
  height: 36px;
  font-size: 16px;
  @include desktop {
    position: absolute; } }

.all {
  position: relative;
  background-color: #ddd;
  padding: 10px;

  $root: &;

  &__wrapper {
    border: 1px solid #777;
    background-color: $white;
    border-radius: 6px;
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px; }
  &__wrapperItem {
    display: flex;
    flex-flow: row wrap;
    padding-top: 5px;
    padding-bottom: 5px;
    // line-height: 1.33
    color: $light;
    &:nth-child(1) {
      border-bottom: 1px solid #ddd; }
    &:nth-child(2) {
      border-bottom: 1px solid #ddd; }
    &:nth-child(3) {
      font-weight: 700;
      color: $black;
      #{$root}__label {
        font-size: 1.6  em; } } }

  &__label {
    position: relative;
    width: 50%;
    font-size: 1.4em;
    line-height: 1.33;
    text-align: left;
    padding-left: 10px; }

  &__taka {
    position: relative;
    width: 50%;
    display: inline-flex;
    align-items: center;
    justify-content: flex-end;
    padding-right: 10px;
    line-height: 1.33;
    font-size: 1.6em;
    // font-weight: 400
    &.joma {
      color: darken($green, 10%); }
    &.baki {
      color: $red; } } }

.filter {
  background-color: $lighter;
  padding: 10px;
  &__label {
    font-size: 14px;
    margin-right: 10px; }
  &__select {
    border: 1px solid $light;
    font-size: 14px; } }

.allList {
  position: relative;
  &__item {
    position: relative;
    // border-bottom: 1px solid #ddd
    display: flex;
    flex-flow: row nowrap;
    // align-items: center
    align-items: stretch;
    text-decoration: none;
    color: $black;
    padding-left: 10px;
    min-height: 50px;
    // padding-right: 10px
    &::before {
      content: "";
      position: absolute;
      height: 1px;
      background-color: #ddd;
      left: 0;
      bottom: 0;
      right: 0;
      margin-bottom: -1px;
      margin-left: 10px;
      margin-right: 10px; } }
  &__label {
    display: inline-flex;
    flex-flow: column nowrap;
    justify-content: center;
    flex: 1;
    font-size: 1.4em;
    line-height: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; }
  &__name {
    // border: 1px solid red
    line-height: 1.22; }
  &__date {
    font-size: 12px;
    color: $light; }

  &__taka {
    display: inline-flex;
    flex-flow: column nowrap;
    justify-content: center;
    font-size: 1.6em;
    line-height: 1.22;
    font-weight: 700;
    width: 105px * 2;
    text-align: right;
    position: relative;
    padding-top: 10px;
    padding-bottom: 10px;
    &.baki {
      color: $red;
      padding-right: 105px + 10; }
    &.joma {
      color: darken($green, 10%);
      padding-right: 10px; }
    &::before {
      content: "";
      position: absolute;
      width: 50%;
      background-color: $lighter;
      left: 0;
      top: 0;
      height: 100%;
      z-index: -1; } } }


